import { DomSanitizer } from '@angular/platform-browser';
import { ViewController, Content } from 'ionic-angular';
import { WidgetProvider } from './../../../providers/widget-provider';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core'; 
/**
 * Generated class for the PrivacyPolicyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-privacy-policy',
  templateUrl: 'privacy-policy.html',
})
export class PrivacyPolicyPage {

  @ViewChild(Content) pageContent: Content;
  lang: string ;
  subjectId: number;

  content: string;
  title: string;
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public translateService: TranslateService,
    public widgetProvider: WidgetProvider,
    public viewCtrl:ViewController,
    public sanitizer: DomSanitizer) {
      this.subjectId = this.navParams.data.subjectId;
      this.lang = this.navParams.data.language;
      
      this.widgetProvider.getPageArticle(this.subjectId,this.lang).subscribe((data)=>{ 
        if(data.errcode == 0){
          this.content = data.result.content;; 
          // this.content += `<div style="background:red;width:100%;height:300px;"></div>`
          if(this.content){
            (this.content as any) = this.sanitizer.bypassSecurityTrustHtml(this.content);
          } 
          this.title = data.result.title;
        }
      })
  }

  ionViewDidLoad() { 
    this.pageContent && this.pageContent.resize()
  }


  goBack(){
    this.viewCtrl.dismiss();
  }
}



